import ph1 from "./photos/whatsapp.png";
import ph2 from "./photos/instagram.png";
import ph3 from "./photos/facebook.png";
import ph4 from "./photos/github.png";
import { useRef } from "react";
export default function Contacts() {
  return (
    <div className="con li " style={{ height: "100vh" }}>
      <Social photo={ph1} info={"+9720597240377"} />
      <Social photo={ph2} info={"m.t.m.a.2004"} />
      <Social photo={ph3} info={"Mohammed Almughanni "} />
      <Social photo={ph4} info={"Mtma2004"} />
    </div>
  );
}
function Social({ photo, info }) {
  let inp = useRef();
  let i = useRef();
  let but = useRef();
  return (
    <>
      <div className="social">
        <img
          style={{
            width: "90px",
            height: "90px",
            borderRadius: "50%",
          }}
          src={photo}
          alt=""
          loading="lazy"
        ></img>
        <div className="socialinfo">
          <input ref={inp} disabled type="text" value={info} />
          <button
            className="copy"
            ref={but}
            onClick={() => {
              console.log(but.current);
              let text = inp.current.value;
              navigator.clipboard.writeText(text).then(() => {
                i.current.classList = [];
                i.current.classList.add("fa-solid");
                i.current.classList.add("fa-check");
                but.current.classList.add("clicked");
                setTimeout(() => {
                  but.current.classList.remove("clicked");
                  i.current.classList = [];
                  i.current.classList.add("fa-regular");
                  i.current.classList.add("fa-copy");
                }, 2000);
              });
            }}
          >
            <i ref={i} className="fa-regular fa-copy"></i>
          </button>
        </div>
      </div>
    </>
  );
}
